<template>
    <div class="CategoryPop" v-if="param.show && param.type == 'add'">
        <div class="mask"></div>
        <div class="popContent">
            <div class="closeIcon" @click="close"></div>
            <div class="panels">
                <div class="title">新增类目</div>
                <div class="content">
                    <div class="inputArea clearFix">
                        <div class="radio" :class="{checked: param.root}" @click="setRoot" :data-root="1"></div>
                        <div class="text">新增为一级类目</div>
                    </div>
                    <div class="inputArea clearFix">
                        <div class="radio" :class="{checked: !param.root}" @click="setRoot" :data-root="0"></div>
                        <div class="text">上级类目</div>
                        <div class="input">
                            <input v-model="parentId" placeholder="请输入上级类目ID" :disabled="param.root ? true : false"/>
                        </div>
                    </div>
                    <div class="inputArea clearFix">
                        <div class="blank"></div>
                        <div class="text">类目名称</div>
                        <div class="input">
                            <input v-model="name" placeholder="请输入类目名称"/>
                        </div>
                    </div>
                    <div class="inputArea clearFix">
                        <div class="text pl80">
                            <div class="checkbox" :class="{checked: param.itemCategory}" @click="setItemCategory"></div>
                            <div class="text">设为品类</div>
                        </div>
                    </div>
                    <div class="buttonArea singleButtonArea">
                        <div class="button" @click="submit">确定</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
	import './index.less';
    import Index from './index.js';
    export default Index;
</script>


