import Vue from 'vue';
import { mapState } from 'vuex';

import CategoryStore from './store';
import FcTree from 'fcbox/tree/index.vue';
import CategoryPop from './pop/index.vue';

const Category =  {
    name: 'Category',
    data() {
		return {};
	},
    components: {
        FcTree,
        CategoryPop
    },
    computed: Object.assign ({
            
        }, mapState({
            title: (state) => state.Category.title,
            treeParam: (state) => state.Category.treeParam,
            addPopParam: (state) => state.Category.addPopParam,
		})
	),
    created() {
        if (!this.$store.state.Category) {
            this.$store.registerModule('Category', CategoryStore);
        }
        document.title = this.title;
    },
    methods: {
        addCategory(e) {
            this.$store.commit('setValue', {
                key: 'Category.addPopParam',
                value: Object.assign(this.addPopParam, {
                    show: true,
                    parentId: 0,
                    name: '',
                    root: true,
                })
            });
        },
        treeCallback(param) {
            switch (param.type) {
                case 'add':
                    this.$store.commit('setValue', {
                        key: 'Category.addPopParam',
                        value: Object.assign(this.addPopParam, {
                            show: true,
                            parentId: param.settingId,
                            name: '',
                            root: false,
                        })
                    });
                    break;
            }
        },
        popCallback(param) {
            console.log(param);
        },
    }
}

export default Category;


