const data = [];
for (var a = 0; a < 10; a++) {
    var id = a + 1;
    data.push({
        id: id,
        parentId: 0,
        name: '分类' + id,
    });
    for (var b = 0; b < 10; b++) {
        var id = (a + 1) * 10 + b + 1;
        data.push({
            id: id,
            parentId: a + 1,
            name: '分类' + id,
        });
        for (var c = 0; c < 10; c++) {
            var id = (a + 1) * 100 + (b + 1) * 10 + c + 1;
            data.push({
                id: id,
                parentId: (a + 1) * 10 + b + 1,
                name: '分类' + id,
            });
            for (var d = 0; d < 10; d++) {
                var id = (a + 1) * 1000 + (b + 1) * 100 + (c + 1) * 10 + d + 1;
                data.push({
                    id: id,
                    parentId: (a + 1) * 100 + (b + 1) * 10 + c + 1,
                    name: '分类' + id,
                });
                for (var e = 0; e < 10; e++) {
                    var id = (a + 1) * 10000 + (b + 1) * 1000 + (c + 1) * 100 + (d + 1)*10 + e + 1;
                    data.push({
                        id: id,
                        parentId: (a + 1) * 1000 + (b + 1) * 100 + (c + 1) * 10 + d + 1,
                        name: '分类' + id,
                    });
                    //for (var f = 0; f < 3; f++) {
                    //    var id = (a + 1) * 100000 + (b + 1) * 10000 + (c + 1) * 1000 + (d + 1)*100 + (e + 1)*10 + f + 1;
                    //    data.push({
                    //        id: id,
                    //        parentId: (a + 1) * 10000 + (b + 1) * 1000 + (c + 1) * 100 + (d + 1)*10 + e + 1,
                    //        name: '分类' + id,
                    //    });
                    //}
                }
            }
        }
    }
}
//console.log(data);

module.exports = {
    title: '类目管理',
    treeParam: {
        theme: 'setList',
        checkedId: '',
        rootId: 0,
        lazy: true,
        showAll: true,
        catch: true,
        height: 520,
        data: data
    },
    addPopParam: {
        type: 'add',
        show: false,
        root: true,
        itemCategory: true,
        name: '',
        parentId: 0
    },
};


