import { mapState } from 'vuex';

const CategoryPop =  {
    name: 'CategoryPop',
    data() {
		return {};
	},
    props: ['param', 'callback'],
    computed: {
        name: {
            get () {
                return this.param.name;
            },
            set (value) {
                this.param.name = value;
            }
        },
        parentId: {
            get () {
                return this.param.parentId;
            },
            set (value) {
                this.param.parentId = value;
            }
        },
    },
    created() {

    },
    methods: {
        close() {
            this.param.show = false;
        },
        setRoot(e) {
            const root = parseInt(e.target.getAttribute('data-root'));
            this.param.root = root;
            this.param.parentId = root ? 0 : '';
        },
        setItemCategory() {
            this.param.itemCategory = !this.param.itemCategory;
        },
        submit() {
            this.callback(this.param);
        }
    }
}

export default CategoryPop;


